<template>
<!--  表格数据-->


  <n-space style="margin-top: 10px">
    <n-input placeholder="输入管理员的姓名" v-model:value="search.name" size="small">
      <template #prefix>
        <n-icon >
          <SearchCircleOutline />
        </n-icon>
      </template>
    </n-input>
    <n-input placeholder="输入地址" v-model:value="search.address" size="small">
      <template #prefix>
        <n-icon >
          <SearchCircleOutline />
        </n-icon>
      </template>
    </n-input>
    <n-input placeholder="输入联系电话" v-model:value="search.phone" size="small">
      <template #prefix>
        <n-icon >
          <SearchCircleOutline />
        </n-icon>
      </template>
    </n-input>

    <n-select size="small" v-model:value="sexValue" :options="sexOptions" style="width: 200px" placeholder="选择性别"/>
    <n-select size="small" v-model:value="wedlockValue" :options="wedlockOptions" style="width: 200px" placeholder="选择婚姻状态"/>
    <n-select size="small" v-model:value="nationValue" :options="nationOptions" style="width: 200px" placeholder="选择民族"/>
    <n-select size="small" v-model:value="polValue" :options="polOptions" style="width: 200px" placeholder="选择政治状态"/>

    <n-button type="info" ghost   @click="findBy"  size="small"><n-icon size="20">
      <SearchCircleOutline />
    </n-icon >搜索</n-button>
    <n-button type="warning"  ghost  @click="clearSearch" size="small"><n-icon size="20">
      <TrashOutline />
    </n-icon >清空选择条件</n-button>
    <n-button type="warning"  disabled  @click="del" size="small"><n-icon size="20">
      <TrashOutline />
    </n-icon >删除选中的数据</n-button>


  </n-space>

  <n-data-table

  ref="table"
  :columns="columns"
  :data="userData"
  :max-height="650"
  :scroll-x="500"
  :row-key="dataKey"
  style="margin-top: 20px"
  @update:checked-row-keys="handleCheck"

  />

  <div style="display: flex;flex-direction: row-reverse;margin-top: 20px">
  <n-pagination :item-count="itemCount" :page-sizes="[5, 10, 15]" showSizePicker :on-update:page="onChangePage"
                :on-update:page-size="onChangePageSize"
  />
  </div>

</template>
<script>
import {ref, h, reactive, onMounted} from 'vue'
import {deleteRequest, getRequest, putRequest} from "../../api/request";
import {useMessage,useDialog,NSwitch,NGradientText,NIcon} from "naive-ui";
import {SearchCircleOutline,PersonAddOutline,TrashOutline} from '@vicons/ionicons5'
import nation from '../../assets/nation.json'
import political from '../../assets/political.json'


const createColumns=({switchChangeSpeak,switchChangeEnable})=>{
  return[
    {
      type:'selection',
      fixed:'left',

    },
    {
      title: '姓名',
      key:'name'
    },
    {
      title: 'username',
      key:'username'
    },
    {
      title:'性别',
      key:'gender'
    },
    {
      title:'电话号码',
      key:'phone'
    },
    {
      title:'地址',
      key:'address'
    },
    {
      title:'婚姻状态',
      key:'wedlock'
    },
    {
      title:'民族',
      key:'actions',
      render(row){
        return h(
            NGradientText,
            {
              type:'info'
            },
            {default:()=>row.nation.name}
        )
      }
    },
    {
      title:'政治状态',
      key:'actions',
      render(row){
        return h(
            NGradientText,
            {

            },
            {default:()=>row.political.name}
        )
      }

    },
    {
      title:'可用状态',
      key:'actions',
      render(row){
        return h(
            NSwitch,
            {
              defaultValue:row.enabled,
              onUpdateValue:()=>switchChangeEnable(row)
            }
        )
      }
    },
    {
      title:'可发言状态',
      key:'actions',
      render(row){
        return h(
            NSwitch,
            {
              // defaultValue:row.speak,
              // value:row.speak,
              defaultValue:row.speak,
              onUpdateValue:()=>switchChangeSpeak(row)
            }
        )
      }
    },

  ]
}

export  default {
  components:{
    SearchCircleOutline,PersonAddOutline,TrashOutline
  },

  setup(props){
    const search=reactive({name:'',address:'',phone:''})
    const message=useMessage()
    const dialog=useDialog()
    const userData=ref([])
    const itemCount=ref()
    const active=ref(false)
    const sexValue=ref(null)
    const wedlockValue=ref(null)
    const nationValue=ref(null)
    const polValue=ref(null)

    const checkedRowKeys=ref([])


    const pagination=reactive({
      page:1,
      pageSize:5,
    })

    // 该页面的数据
    function onChangePage(page){
      pagination.page=page
      getAllUser()
      // message.success(page)
    }
    function onChangePageSize(pageSize){
      pagination.pageSize=pageSize
      getAllUser()
      // message.success(pageSize)
    }
function clearSearch() {
  search.name='';
  sexValue.value='';
  wedlockValue.value='';
  nationValue.value='';
  polValue.value=''
}

function getAllUser() {
  getRequest('/user/basic/getAll/?page='+pagination.page+"&&size="+pagination.pageSize).then(resp=>{
    userData.value=resp.data.data;
    itemCount.value=resp.data.total
    console.log(userData)
  })
}
function del() {
      console.log(checkedRowKeys.value)
      deleteRequest('/user/basic/?delIds='+checkedRowKeys.value).then(resp=>{
        getAllUser()
      })

}

function findBy() {
      let url='/user/basic/getAll/?page='+pagination.page+"&&size="+pagination.pageSize;
      if (search.name){
        url+='&&name='+search.name;
      }
      if (sexValue.value){
        url+='&&gender='+sexValue.value;
      }
      if (search.address){
        url+='&&address='+search.address;
      }
      if (search.phone){
        url+='&&phone='+search.phone
      }
      if (wedlockValue.value){
        url+='&&wedlock='+wedlockValue.value
      }
      if (nationValue.value){
        url+='&&nationId='+nationValue.value
      }
      if(polValue.value){
        url+='&&politicId='+polValue.value
      }

      getRequest(url).then(resp=>{
        userData.value=resp.data.data;
        itemCount.value=resp.data.total;
      })

}
onMounted(getAllUser)
    return{
      del,
      checkedRowKeys,
      handleCheck (rowKeys) {
        checkedRowKeys.value = rowKeys
      },

      polValue,
      nationValue,
      wedlockValue,
      clearSearch,
      findBy,
      search,
      //分页
      active,
      itemCount,
      onChangePage,
      onChangePageSize,
      pagination,
        userData,
        getAllUser,
      sexValue,
      nationOptions:JSON.parse(JSON.stringify(nation)),
      polOptions:JSON.parse(JSON.stringify(political)),
      wedlockOptions:[
        {
          label: '未婚',
          value: '未婚'
        },
        {
          label: '已婚',
          value: '已婚'
        },
        {
          label: '离异',
          value: '离异'
        },
        {
          label: '请选择婚姻状态',
          value: ''
        },
      ],
      sexOptions:[
        {
          label: '男',
          value: '男'
        },
        {
          label: '女',
          value: '女'
        },
        {
          label: '请选择性别',
          value: ''
        },
      ],
        dataKey: (userData) => {
          return userData.key = userData.id;
        },
        columns: createColumns({
          switchChangeEnable(row){
           putRequest('/user/basic/updateEnabled/?id='+row.id).then(resp=>{
             if (resp){
               message.success("成功更新可用状态！")
             }

           })

          },
          switchChangeSpeak(row){
            putRequest('/user/basic/updateSpeak/?id='+row.id).then(resp=>{
              if(resp){
                message.success("成功更新可留言状态！")
              }
            })
          }

        })
      }
  }
}
</script>
